import React, { memo } from 'react'

import { UseAuthContext } from '../auth-context'
import Button from '../../../core/button'
import ProfilePicture from './profile-picture'

const GuestButton = () => {
  const { logout, name, image } = UseAuthContext()
  return name ? <Button title={`Welcome ${name}`} background='logout' onClick={logout} subTitle='Click here to sign out' icon={image && <ProfilePicture image={image} />} /> : null
}

export default memo(GuestButton)
