import React, { memo, useEffect } from 'react'
import PropTypes from 'prop-types'

import { useTimer } from '../../quiz/timer'
import {
  CircleBackground,
  CircleProgress,
  CircleText
} from './styled'

const WaitTimer = ({ zeroText, time, type, size, strokeWidth, strokeColor, strokeProgress, onFinish, setTimer }) => {
  const timer = useTimer({
    initialTime: time,
    timerType: type
  })

  useEffect(() => {
    setTimer && setTimer(timer)
  }, [timer.time])

  useEffect(() => {
    timer.start()
  }, [])

  useEffect(() => {
    if (timer.time <= 0) {
      timer.pause()
      if (typeof (onFinish) === 'function') {
        onFinish()
      }
    }
  }, [timer.time])

  useEffect(() => {
    return () => {
      timer.reset()
    }
  }, [])

  // Size of the enclosing square
  const sqSize = size
  // SVG centers the stroke width on the radius, subtract out so circle fits in square
  const radius = (size - strokeWidth) / 2
  // Enclose cicle in a circumscribing square
  const viewBox = `0 0 ${size} ${size}`
  // Arc length at 100% coverage is the circle circumference
  const dashArray = radius * Math.PI * 2
  // Scale 100% coverage overlay with the actual time
  const dashOffset = dashArray - dashArray * timer.time / time // initialtime

  return (
    <svg
      width={sqSize}
      height={sqSize}
      viewBox={viewBox}>
      <CircleBackground
        className='circle-background'
        cx={sqSize / 2}
        cy={sqSize / 2}
        r={radius}
        strokeWidth={`${strokeWidth}px`}
        strokeColor={strokeColor}
      />
      <CircleProgress
        className='circle-progress'
        cx={sqSize / 2}
        cy={sqSize / 2}
        r={radius}
        dashArray={dashArray}
        strokeWidth={`${strokeWidth}px`}
        strokeProgress={strokeProgress}
        // Start progress marker at 12 O'Clock
        transform={`rotate(-90 ${sqSize / 2} ${sqSize / 2})`}
        style={{
            strokeDasharray: dashArray,
            animation: `dash ${time}s linear forwards`,
            strokeDashoffset: -dashOffset
          }}
        />
      <CircleText
        className='circle-text'
        x='50%'
        y='50%'
        dy='.3em'
        textAnchor='middle'>
        {
           timer.time < 1
           ? zeroText
           : Math.floor(timer.time)
        }
      </CircleText>
    </svg>
  )
}

WaitTimer.defaultProps = {
  time: 10,
  type: 'DECREMENTAL',
  size: 120,

  strokeWidth: 6,
  strokeColor: '#FFFFFF',
  strokeProgress: '#7C56E9',
  zeroText: 'GO!'
}

WaitTimer.propTypes = {
  time: PropTypes.number,
  type: PropTypes.oneOf([
    'DECREMENTAL',
    'INCREMENTAL'
  ]),
  size: PropTypes.number,
  strokeWidth: PropTypes.number,
  strokeColor: PropTypes.string,
  strokeProgress: PropTypes.string,

  onFinish: PropTypes.func,
  zeroText: PropTypes.string,

  setTimer: PropTypes.func
}

export default memo(WaitTimer)
