export const getPlayersTopThree = (players, responses, currentQuestion) => {
  const wrongPlayers = players.filter(player => {
    return getPlayerResponse(player, responses).answer !== currentQuestion.correct
  }).sort((playerA, playerB) => getPlayerResponse(playerA, responses).time - getPlayerResponse(playerB, responses).time)

  return wrongPlayers.slice(0, 3)
}
