import React from 'react'
import PropTypes from 'prop-types'

import styled, { css } from 'styled-components'
import { useTheme } from '@material-ui/core/styles'
import { Icons } from '../../helpers/icons'

import isNumber from 'lodash/isNumber'

const BigPlayerImage = styled.div`
  width: ${({ size }) => size || '50px'};
  height: ${({ size }) => size || '50px'};
  background-image: url(${({ src }) => src});
  opacity: 50%;
  background-color: #efefef;
  background-size: cover;
  margin: 0 auto;
  border-radius: 100%;
  border: ${({ showBorder, color, count }) => showBorder || isNumber(count)
    ? `4px solid ${color}` : 'none'};

  transition: border ease-in-out 0.1s;
  -webkit-transition: border ease-in-out 0.1s;
  -moz-transition: border ease-in-out 0.1s;

  ${({ interactive }) => interactive && css`
    &:active {
      border-width: 6px;
    }
  `}
`
const Checkmark = styled.div`
  width: 24px;
  height: 24px;
  position: absolute;
  top: -8px;
  right: -8px;
  border-radius: 100%;
`

const IconWrapper = styled.div`
  position: absolute;
  top: -8px;
  right: -8px;
  background-color: ${({ color }) => color};
  border-radius: 100%;

  justify-content: center;
  display: flex;
  align-items: center;

  width: 30px;
  height: 30px;

  span {
    color: white;
  }
`

const Wrapper = styled.div`
  position: relative;
  float: left;
`

const Overlay = styled.div`
  width: 50px;
  height: 50px;
  background: #424242;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 100%;
  opacity: 0.8;
`

const Player = ({ player, hasAnswered, darker, size, count, borderColorIndex }) => {
  const { image } = player

  const { palette } = useTheme()
  const color = [
    palette.primary.orange,
    palette.primary.blue,
    palette.primary.green
  ][borderColorIndex]

  return (
    <Wrapper>
      { hasAnswered && <Checkmark>{Icons.checkmark}</Checkmark> }
      { isNumber(count) && <IconWrapper color={color}><span>{count}</span></IconWrapper> }
      { !hasAnswered && darker && <Overlay /> }
      <BigPlayerImage
        size={size}
        src={image}
        showBorder={hasAnswered}
        color={color}
        count={count}
        interactive={isNumber(count)}
       />
    </Wrapper>
  )
}

Player.propTypes = {
  player: PropTypes.object.isRequired,
  hasAnswered: PropTypes.bool,
  darker: PropTypes.bool,
  size: PropTypes.number,
  count: PropTypes.number,
  borderColorIndex: PropTypes.number
}

Player.defaultProps = {
  borderColorIndex: 2
}

export default Player
