import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import { withStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'

import axios from 'axios'

const styles = theme => ({
  button: {
    margin: theme.spacing(1)
  }
})

const DatabaseTest = (props) => {
  const [data, setData] = useState([])
  const [polling, pollingForData] = useState(false)
  const [message, setMessage] = useState(null)
  const [idToDelete, setIdToDelete] = useState(null)

  useEffect(() => {
    getDataFromDb()

    // didmount
    if (!polling) {
      const interval = setInterval(getDataFromDb, 5000)
      pollingForData(interval)
    }

    // unmmount
    return clearPolling()
  }, [])

  const clearPolling = () => {
    if (polling) {
      clearInterval(polling)
      pollingForData(null)
    }
  }

  const getDataFromDb = () => {
    axios.get('/api/getData')
      .then(res => setData(res.data.data))
  }

  const postDataToDB = () => {
    let currentIds = data.map(data => data.id)
    let idToBeAdded = 0
    while (currentIds.includes(idToBeAdded)) {
      ++idToBeAdded
    }

    axios.post('/api/postData', {
      id: idToBeAdded,
      message: message
    })
  }

  const deleteDataFromDB = () => {
    let itemId = null

    data.forEach(item => {
      if (item.id === parseInt(idToDelete)) {
        itemId = item._id
      }
    })
    axios.delete('/api/deleteData', {
      data: {
        id: itemId
      }
    })
  }

  const { classes } = props

  return (
    <div>
      <ul>
        {data.length <= 0
        ? 'NO DB ENTRIES YET'
        : data.map((item, index) => (
          <li style={{ padding: '10px' }} key={index}>
            <span style={{ color: 'gray' }}> id: </span> {item.id} <br />
            <span style={{ color: 'gray' }}> data: </span>
            {item.message}
          </li>
          ))}
      </ul>
      <div style={{ padding: '10px' }}>
        <input
          type='text'
          onChange={e => setMessage(e.target.value)}
          placeholder='add something in the database'
          style={{ width: '200px' }}
            />
        <Button className={classes.button} variant='contained' color='secondary' onClick={() => postDataToDB(message)}>
              ADD
        </Button>
      </div>
      <div style={{ padding: '10px' }}>
        <input
          type='text'
          style={{ width: '200px' }}
          onChange={e => setIdToDelete(e.target.value)}
          placeholder='put id of item to delete'
          />
        <Button className={classes.button} variant='contained' color='primary' onClick={deleteDataFromDB}>
            DELETE
        </Button>

      </div>

    </div>
  )
}

DatabaseTest.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(DatabaseTest)
