import React, { useEffect, useState, memo } from 'react'
import PropTypes from 'prop-types'
import { withRouter } from 'react-router'

import axios from 'axios'
import isEmpty from 'lodash/isEmpty'

import Grid from '@material-ui/core/Grid'
import CategoryCard from './card'
import Background from '../background'

import Typography from '@material-ui/core/Typography'
import styled from 'styled-components'
import { useShootsContext } from '../../context/shoots-context'
import { UseAuthContext } from '../../context/auth'

const DUMMY_USER = 'Bassie100'
const Container = styled.div`
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
  height: 100%;
`
const Header = styled.div`
  width: 100%;
  padding: ${({ theme }) => theme.spacing(4) + 'px'};
`

const Categories = ({ history }) => {
  const [categories, setCategories] = useState()
  const { name } = UseAuthContext()
  const { actions } = useShootsContext()

  useEffect(() => {
    // REFACTOR save this in localstorage
    axios.get('/api/categories')
      .then(res => { setCategories(res.data.data) })
  }, [])

  const handleCategoryClick = (identifier, categoryName) => {
    const data = {
      hostName: name || DUMMY_USER,
      category: identifier,
      categoryName: categoryName,
      gameType: 'shoots'
    }

    actions.createGame(data)

    history.push(`/waiting`)
  }

  console.log(categories)

  return (
    <Background align='flex-start'>
      <Container>
        <Header>
          <Typography variant='h6' component='h2'>
            Kies een categorie
          </Typography>
        </Header>
        <Grid
          container
          direction='row'
          alignItems='center'
          spacing={2}
        >
          {
          !isEmpty(categories)
            ? categories.data.map(({ categoryId, icon, categoryName, subtitle, color }) =>
            (
              <Grid item xs={6} key={categoryId}>
                <CategoryCard
                  id={categoryId}
                  icon={icon}
                  title={categoryName}
                  subtitle={subtitle}
                  color={color}
                  onClick={() => handleCategoryClick(categoryId, categoryName)}
              />
              </Grid>
            ))
            : <div />
        }
        </Grid>
      </Container>
    </Background>
  )
}

Categories.propTypes = {
  history: PropTypes.object.isRequired
}

export default memo(withRouter(Categories))
