import React, { memo } from 'react'

import Button from './../../../core/button'
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props'
import ProfilePicture from './profile-picture'
import { UseAuthContext } from '../auth-context'

const FacebookButton = () => {
  const { succesFacebook, image, facebook, name } = UseAuthContext()
  return (
    <FacebookLogin
      appId='475749869631679'
      fields='name,email,picture'
      callback={succesFacebook}
      render={renderProps => (
        <Button
          title={facebook && name ? `Hi ${name}!` : 'Facebook'}
          background='facebook'
          icon={facebook && image ? <ProfilePicture image={image} /> : 'Facebook'}
          iconPosition='right'
          onClick={renderProps.onClick}
            />
        )}
      />
  )
}

export default memo(FacebookButton)
