import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

// unicode not working, so do it like this
const renderEmoji = (name) => ({
  classic: '😌',
  party: '🥳',
  gek: '🤪',
  random: '🥺'
})[name]

const Styledspan = styled.span(props => ({
  fontSize: props.size,
  // unicode not working
  '&:after': {
    content: props.symbol
  }
}))

const Emoji = ({ label, symbol, size }) => (
  <Styledspan
    className='emoji'
    aria-label={label}
    aria-hidden={false}
    role='img'
    symbol={symbol}
    size={size}
    >
    {renderEmoji(label.trim().toLowerCase())}
  </Styledspan>
)

Emoji.defaultProps = {
  label: 'emoji',
  symbol: 'U+1F601',
  size: '40px'
}

Emoji.propTypes = {
  label: PropTypes.string,
  symbol: PropTypes.string.isRequired,
  size: PropTypes.string
}

export default Emoji
