import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import Status from './status'

import SVGIcon from './svg-icon'
import { Icons } from '../../helpers/icons'

import { Funny } from './messages'
import { withRouter } from 'react-router'

import isNumber from 'lodash/isNumber'

import { useShootsContext } from '../../context/shoots-context'

// const getRandomFromArray = xs => xs[Math.floor(Math.random() * xs.length)]
const getRandomFunnySentences = () => Funny.slave

const SlaveWaiting = ({ history }) => {
  const { gameState, shootsGame } = useShootsContext()

  const [index, setIndex] = useState(0)
  const [message, setMessage] = useState()

  useEffect(() => {
    getMessages()
  }, [])

  useEffect(() => {
    const interval = setInterval(() => {
      setIndex(index + 1)
    }, 2000)
    return () => clearInterval(interval)
  }, [])

  const getMessages = () => {
    const playersWaitingCaption = `${shootsGame.currentGameMaster.name} is de vraag aan het beantwoorden. Dit kan eventjes duren.`
    const captions = [playersWaitingCaption, ...getRandomFunnySentences()]

    return setMessage(captions[index])
  }

  const gameMaster = gameState.players.find(player => player.id === shootsGame.currentGameMaster.id)

  useEffect(() => {
    if (isNumber(shootsGame.getCurrentQuestion.correct)) {
      history.push('/quiz-slave')
    }
  })

  return (
    <Status
      title='Momentje'
      caption={message}
      icon={<SVGIcon icon={Icons.hourglass} />}
      waitingForGameMaster
      players={[gameMaster]}
    />
  )
}

SlaveWaiting.propTypes = {
  history: PropTypes
}

export default withRouter(SlaveWaiting)
