import React, { memo } from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

import { UseAuthContext } from '../../context/auth'

import { Grid, Typography } from '@material-ui/core'
import ProfilePicture from '../../context/auth/consumers/profile-picture'
import { Icons } from '../../helpers/icons'
import { useShootsContext } from '../../context/shoots-context'
// import { howMuchPlayerDrink } from '../../helpers/shoots-helper'

const Golden = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F79D1B; // iets donker gold
`

const StyledDrinks = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px;
`

const formatName = name => name && name.length > 15 ? name.split(' ')[0] : name

const PlayerPosition = styled(Grid)`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items:center;
  margin-top: 16px !important;
  text-align: left;
  border-top: 1px solid #946DFF;
  background: ${({ highlight, theme }) => highlight ? theme.palette.background.highlighted : 'none'};

  * {
    color: white
  }
`

const PlayersList = ({ pin, waiting, players, responses, currentQuestion, gameMaster }) => {
  const { authenticated, name } = UseAuthContext()
  const { shootsGame } = useShootsContext()
  // console.log(drinks)
  console.log('PLAYERS view', authenticated)
  console.log(gameMaster)

  const showIconOrPoints = (variant, player, isGameMaster) => {
    if (waiting) {
      if (isGameMaster) {
        return Icons.crown
      } else {
        return null
      }
    } else {
      return <StyledDrinks>
        <Golden>
          <Typography variant={variant}>{shootsGame.drinks ? (shootsGame.drinks[player.id] || 0) : '-'}x</Typography>
        </Golden>
        {Icons.smallDrink}
      </StyledDrinks>
    }
  }

  return (
    players.map((player, i) => {
      console.log(name, player.name)
      const isHost = player.isHost
      const you = name === player.name
      const variant = you ? 'h6' : 'body2'

      const getResponseTime = (player) => {
        const response = responses && responses.find(response => response.playerId === player.id)
        return response && response.time
      }

      const responseTime = getResponseTime(player)

      console.log(gameMaster, player)

      return <PlayerPosition key={`player_${i}`} container spacing={4} highlight={you.toString()}>
        <Grid item xs={2}>
          {
            !waiting && <Typography className='pl-2' variant={variant}>{i + 1}</Typography>
          }
        </Grid>
        <Grid item xs={2}>
          <ProfilePicture style={{ marginLeft: -16 }} image={player.image} />
        </Grid>
        <Grid container item xs={4}>
          <Typography display='inline' variant='h6'>{formatName(player.name)}</Typography>
          { waiting && isHost && <Typography style={{ width: '100%' }} display='inline' variant='subtitle1'>Game host</Typography>}
          {
            !waiting && <Typography variant='body2' style={{ width: '100%' }} display='inline'>
              {
                gameMaster.id === player.id ? 'GameMaster'
                : `${Math.round(responseTime / 100) / 10}s`
              }
            </Typography>
          }
        </Grid>
        <Grid container justify='flex-end' item xs={4}>
          <Grid item>
            {
             showIconOrPoints(variant, player, waiting && isHost)
            }
          </Grid>
        </Grid>
      </PlayerPosition>
    })

  )
}

PlayersList.defaultProps = {
  waiting: false,
  pin: null
}

PlayersList.propTypes = {
  waiting: PropTypes.bool,
  pin: PropTypes.number,
  responses: PropTypes.array,
  currentQuestion: PropTypes.object
}

export default memo(PlayersList)
