import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import Status from './status'

import SVGIcon from './svg-icon'
import { Icons } from '../../helpers/icons'

import { withRouter } from 'react-router'
import { useShootsContext } from '../../context/shoots-context'

const RoundDone = ({ history }) => {
  const { shootsGame, isWinner } = useShootsContext()
  const [count, setCount] = useState(0)

  if (count === 4) {
    if (isWinner()) {
      history.push('/winner')
    } else {
      history.push('/scores')
    }
  }

  useEffect(() => {
    const timeout = setInterval(() => {
      setCount(prev => prev + 1)
    }, 1000)
    return () => clearInterval(timeout)
  }, [])

  return (
    <Status
      title={`${shootsGame.currentQuestion} van ${shootsGame.questionsPerRound} ronde ${shootsGame.currentRound}, over ${shootsGame.currentGameMaster.name}`}
      caption='Tijd voor de winnaar om de drankjes uit te delen!'
      icon={<SVGIcon icon={Icons.drink} color={'#ff8cad'} strokeColor={'#9b76ff'} showIcons />}
    />
  )
}

RoundDone.propTypes = {
  history: PropTypes
}

export default withRouter(RoundDone)
