import React, { memo, useState } from 'react'
import PropTypes from 'prop-types'

import Button from '../../core/button'
import { withStyles } from '@material-ui/core'

import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import Timer from '../status/wait-timer'
import styled from 'styled-components'

const ButtonColors = ['purple', 'blue', 'orange', 'pink']
const Alphabet = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'

const QUESTION = 'question'
const CORRECT = 'correct'
const WRONG = 'wrong'
const DISABLED = 'disabled'

const Question = styled.p`
  font-size: 21px;
    display: block;
    font-weight: bold;
    color: #242970;
`

const styles = theme => ({
  wrapper: {
    'box-sizing': 'border-box',
    'display': 'flex',
    'flex-direction': 'column',
    'justify-content': 'center',
    'align-items': 'center',
    'height': '95hv'
  },
  card: {
    maxWidth: 377,
    paddingBottom: 20
  },
  cardContent: {
    paddingBottom: 0
  },
  emoji: {
    fontSize: 40
  },
  intro: {
    textTransform: 'uppercase'
  }
})

const Quiz = ({ classes, time, question, answers, correct, onFinish, currentPart, totalParts, onSelect }) => {
  const [selectedAnswer, setSelectedAnswer] = useState(null)
  const [stepFinshed, setStepFinished] = useState(false)
  const [timer, setTimer] = useState(0)

  const handleButtonClick = index => {
    setSelectedAnswer(index)
    setStepFinished(true)

    setTimeout(() => {
      const answerTime = time - timer.time * 1000
      if (!stepFinshed) {
        onSelect(index, answerTime)
      }
    }, 1500)
  }

  const getButtonCollor = index => {
    let i = index
    while (i > ButtonColors.length - 1) {
      i -= ButtonColors.length
    }
    return ButtonColors[i]
  }

  const getButtonStatus = index => {
    if (stepFinshed) {
      if (selectedAnswer === null) {
        return DISABLED
      }
      if (correct === index || (correct === null && selectedAnswer === index)) {
        return CORRECT
      } else if (correct !== selectedAnswer && selectedAnswer === index) {
        return WRONG
      } else {
        return DISABLED
      }
    }
    // not finished yet
    if (selectedAnswer !== null && selectedAnswer !== index) {
      return DISABLED
    } else {
      return QUESTION
    }
  }

  return <div className='container'>
    <div className={classes.wrapper}>
      <Card className={classes.card}>
        <CardContent className={classes.cardContent}>
          <div className='mb-4'>
            <Timer
              stop={selectedAnswer !== null}
              setTimer={setTimer}
              time={time / 1000}
              onFinish={() => {
                if (selectedAnswer !== null) {
                  onSelect(selectedAnswer, time - timer.time * 1000)
                }
                onFinish()
              }
            }
              size={100}
            />
          </div>
          {
            currentPart && <Typography gutterBottom variant='subtitle1'>
                {currentPart + 1} / {totalParts}
              </Typography>
          }
          <Question>
            {question}
          </Question>
        </CardContent>
        {
          answers.map((question, i) => {
              const status = getButtonStatus(i)
              return <Button
                title={question}
                letter={Alphabet[i]}
                key={i}
                disabled={status === DISABLED}
                variant={status === DISABLED ? QUESTION : status}
                background={getButtonCollor(i)}
                onClick={() => handleButtonClick(i)}
              />
          })
        }
      </Card>
    </div>
  </div>
}

Quiz.propTypes = {
  classes: PropTypes.object.isRequired,
  time: PropTypes.number.isRequired,
  // quizPart: PropTypes.object,
  question: PropTypes.string,
  answers: PropTypes.array,
  correct: PropTypes.number,
  onFinish: PropTypes.func,
  currentPart: PropTypes.number,
  totalParts: PropTypes.number,
  onSelect: PropTypes.func
}

Quiz.defaultProps = {
  correct: null
}

export default memo(withStyles(styles)(Quiz))
