import React from 'react'
import ReactDOM from 'react-dom'

import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import styled from 'styled-components'

import Dialog from '@material-ui/core/Dialog'

import MuiDialogTitle from '@material-ui/core/DialogTitle'
import MuiDialogContent from '@material-ui/core/DialogContent'
import MuiDialogActions from '@material-ui/core/DialogActions'

import Slide from '@material-ui/core/Slide'

import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'
import Button from '../../core/button'

const styles = theme => ({
  root: {
    margin: 0,
    padding: theme.spacing(2)
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500]
  },
  text: {
    color: theme.palette.primary.dark + '!important'
  }
})

const IllustrationHolder = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
  overflow: hidden;
`

const Illustration = styled.img`
  width: 160px;
  height: 160px;
  border: none;
  outline: none;
  object-fit: cover;
`

const Transition = React.forwardRef(function Transition (props, ref) {
  return <Slide direction='up' ref={ref} {...props} />
})

const DialogTitle = withStyles(styles)(props => {
  const { classes, onClose } = props
  return (
    <MuiDialogTitle disableTypography className={classes.root}>
      {
        onClose
        ? (
          <IconButton aria-label='Close' className={classes.closeButton} onClick={onClose}>
            <CloseIcon />
          </IconButton>
        )
      : null
    }
    </MuiDialogTitle>
  )
})

const DialogContent = withStyles(theme => ({
  root: {
    textAlign: 'center',
    padding: theme.spacing(2)
  }
}))(MuiDialogContent)

const DialogActions = withStyles(theme => ({
  root: {
    margin: 0,
    padding: theme.spacing()
  }
}))(MuiDialogActions)

const Modal = ({ classes, isShowing, onClose, content: { title, content, img, buttons } }) =>
  isShowing
    ? (
      ReactDOM.createPortal(
        <div className='modal'>
          <Dialog
            onClose={onClose}
            aria-labelledby='dialog-title'
            TransitionComponent={Transition}
            keepMounted
            open
      >
            <DialogTitle id='dialog-title' onClose={onClose} />
            <DialogContent>
              {
                img &&
                <IllustrationHolder>
                  <Illustration src={img} alt='instructional' />
                </IllustrationHolder>
              }
              <Typography className={classes.text} variant='h6' gutterBottom>
                {title}
              </Typography>
              <Typography className={classes.text} variant='body1' gutterBottom>
                {content}
              </Typography>
            </DialogContent>
            <DialogActions style={{ padding: 0 }}>
              {
               buttons.map(({ color, title, onClick }) => <Button key={title} background={color} title={title} onClick={onClick} variant='fullWidth' />)
              }
            </DialogActions>
          </Dialog>
        </div>,
        document.getElementById('portal')
      )
    )
    : null

Modal.propTypes = {
  classes: PropTypes.object.isRequired,
  isShowing: PropTypes.bool,
  onClose: PropTypes.func.isRequired,
  content: PropTypes.object.isRequired
}

export default withStyles(styles)(Modal)
