const Winner = ({ history }) => {
  const { gameState, shootsGame, gameActions, getWinner } = useShootsContext()

  const playersDrinkTopThree = getPlayersTopThree(gameState.players, shootsGame.responses, shootsGame.getCurrentQuestion)

  const [totalDrinks, setTotalDrinks] = useState(0)
  const [drinks, setDrinks] = useState(playersDrinkTopThree.reduce((a, player) => ({
    ...a,
    [player.id]: 0
  }), {}))

  const DRINKS = 4

  const addDrink = (player) => {
    if (totalDrinks < DRINKS) {
      setDrinks(prev => ({
        ...prev,
        [player.id]: prev[player.id] + 1
      }))

      setTotalDrinks(prev => prev + 1)
    }
  }

  useEffect(() => {
    if (shootsGame.drinks && Object.keys(shootsGame.drinks).length > 0) {
      history.push('/scores')
    }
  })

  const notEnoughDrinks = totalDrinks < DRINKS

  return <Background showCircles>
    <Confetti />
    <Grid
      container
      direction='column'
      justify='center'
      alignItems='center'
      style={{ height: '100%' }}
      >
      <Grid>
        <span style={{ fontSize: 68 }}>🎉</span>
      </Grid>
      <Grid>
        <Typography variant='h6'>Wow!</Typography>
      </Grid>
      <Grid>
        <Typography variant='h6' className='mb-4'>Je bent de winnaar!</Typography>
      </Grid>
      <Grid>
        <Typography>Je mag {Array(DRINKS).fill().map((_, i) => <ShotsWrapper disabled={i < totalDrinks}>{Icons.smallDrink}</ShotsWrapper>)} uitdelen.</Typography>
      </Grid>
      <Grid>
        <Typography>Tap op een speler om een <b>drankje</b> te geven.</Typography>
      </Grid>
      <Grid className='mb-4'>
        <Typography><b>Meerdere taps</b> zijn meerdere drankjes</Typography>
      </Grid>
      <Grid className='mb-4'>
        <Grid container style={{}}>
          {gameState.players.filter(p => getWinner().id !== p.id).map((player, i) => {
                return <PlayerButton onClick={() => addDrink(player)} xs>
                  <Player size={'64px'} player={player} count={drinks[player.id]} borderColorIndex={!notEnoughDrinks ? 2 : i} />
                </PlayerButton>
            })}
        </Grid>
      </Grid>
      <Grid>
        <Button
          title='Fuck em up!'
          background='pink'
          onClick={() => gameActions.fuckThemUp(drinks)}
          disabled={notEnoughDrinks}
          upperCase
        //   variant={notEnoughDrinks ? 'disabled' : 'undefined'}
        />
      </Grid>
    </Grid>
  </Background>
}

Winner.propTypes = {
  history: PropTypes.object.isRequired
}

export default memo(Winner)
