import React, { memo } from 'react'
import PropTypes from 'prop-types'
import WaitRoom from '../wait-room'
import { useShootsContext } from '../../context/shoots-context'

const Waiting = ({ classes, history }) => {
  const { gameState } = useShootsContext()
  return <WaitRoom waiting players={gameState.players} />
}

Waiting.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired
}

export default memo(Waiting)
