import React, { useState } from 'react'
import PropTypes from 'prop-types'

import { makeStyles } from '@material-ui/core/styles'
import MobileStepper from '@material-ui/core/MobileStepper'

import Button from '@material-ui/core/Button'
import SwipeableViews from 'react-swipeable-views'

import styled from 'styled-components'

import ReactCSSTransitionGroup from 'react-addons-css-transition-group'

import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'

import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'

import Slide from './slide'

const useStyles = makeStyles(theme => ({
  root: {
    height: '100%',
    position: 'relative',
    borderRadius: 0,
    background: '#fefefe',
    border: 'none'
  },
  content: {
    margin: 0,
    padding: '0px !important'
  },
  dots: {
    justifyContent: 'center',
    background: '#fefefe'
  }
}))

const StyledButton = styled(Button)`
  width: 100%;
  height: 55px;
  color: ${props => props.textcolor} !important;
  background: ${props => props.background} !important;
  border-radius: 0px !important;
`

const DialogHeader = styled.div`
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  height: 55px;
  position: absolute;
  top: 0;
  left: 0;
  padding: 12px;
  z-index: 2;
`

const Stepper = ({ steps, onClose, fullScreen, maxWidth, transition, open }) => {
  const classes = useStyles()
  const maxSteps = steps.length

  const [activeStep, setActiveStep] = useState(0)
  const [nextButtonTitle, setNextbuttonTitle] = useState('Next')

  const handleNext = (repeat = false) => {
    const reachedEnd = activeStep === maxSteps - 1
    // if we are at the last slide
    // start at the beginning again
    if (reachedEnd && typeof (onClose) === 'function') {
      closeDialog()
      return
    }
    if (repeat && reachedEnd) {
      setActiveStep(prevActiveStep => (prevActiveStep = 0))
    } else {
      // setActiveStep(prevActiveStep => prevActiveStep + 1);
      handleStepChange(activeStep + 1)
    }
  }

  // needed because Dialog is kept mounted,
  // since we are calling this comp through another comp in start.js as <InstructionsDialog />
  const closeDialog = () => {
    setTimeout(() => {
      setActiveStep(0)
      setNextbuttonTitle('Next')
    }, 250)
    onClose()
  }

  // const handleBack = () => {
  //   setActiveStep(prevActiveStep => prevActiveStep - 1);
  // };

  const handleStepChange = step => {
    const reachedEnd = step === maxSteps - 1
    if (reachedEnd) {
      setNextbuttonTitle('Close')
    } else {
      setNextbuttonTitle('Next')
    }
    setActiveStep(step)
  }

  return (
    <Dialog
      open={open}
      TransitionComponent={transition}
      maxWidth={maxWidth}
      onClose={closeDialog}
      fullScreen={fullScreen}
      aria-labelledby='alert-dialog-slide-instructions'
      >
      <DialogContent>
        <DialogHeader>
          <IconButton edge='end' color='inherit' onClick={closeDialog} aria-label='close'>
            <CloseIcon />
          </IconButton>
        </DialogHeader>
        <SwipeableViews
          onChangeIndex={handleStepChange}
          axis={'x'}
          index={activeStep}
          enableMouseEvents
      >
          {steps.map(({ illustration, title, description }, index) => (
            <ReactCSSTransitionGroup
              key={index}
              transitionName='example'
              transitionEnterTimeout={500}
              transitionLeaveTimeout={300}
            >
              <Slide
                illustration={illustration}
                title={title}
                description={description}
            />
            </ReactCSSTransitionGroup>
        ))}
        </SwipeableViews>
        <MobileStepper
          variant='dots'
          steps={maxSteps}
          position='static'
          activeStep={activeStep}
          className={classes.dots}
      />
      </DialogContent>
      <DialogActions style={{ padding: 0 }}>
        {/* <ButtonHolder> */}
        {/* <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
          <KeyboardArrowLeft />
          Back
        </Button> */}
        <StyledButton
          size='small'
          textcolor='#ffffff'
          background='#5da3f2'
          disableRipple
          onClick={handleNext}
          disabled={activeStep === 5}
        >
          {nextButtonTitle}
        </StyledButton>
        {/* </ButtonHolder> */}
      </DialogActions>
    </Dialog>
  )
}

Stepper.defaultProps = {
  fullScreen: false,
  open: false,
  maxWidth: 'md'
}

Stepper.propTypes = {
  fullScreen: PropTypes.bool.isRequired,
  open: PropTypes.bool.isRequired,
  transition: PropTypes.any,
  onClose: PropTypes.func.isRequired,
  steps: PropTypes.array.isRequired,
  maxWidth: PropTypes.string
}

export default Stepper
