import React, { useEffect } from 'react'
import PropTypes from 'prop-types'

import Typography from '@material-ui/core/Typography'
import Background from '../background'

import styled from 'styled-components'
import PlayerStatus from '../player-status/container'

import { useTimer } from '../../components/quiz/timer'

const StyledContainer = styled.div`
  width: 100%;
  height: 100%;
  overflow: hidden;
  display : flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  padding: ${({ theme }) => theme.spacing(2) + 'px'};
`

const StyledHeader = styled.div`
  text-transform: uppercase;
  text-align: center;
  margin-bottom: ${({ theme }) => theme.spacing(4) + 'px'};
`

const StyledFunnyText = styled.div`
  text-align: center;
  margin-top: ${({ theme }) => theme.spacing(4) + 'px'};
  text-transform: uppercase;
  color: ${({ theme }) => theme.palette.white};
`

const Footer = styled.div`
  position: absolue;
  bottom: 80px;
  right: auto;
  left: auto;
`

const Status = ({ title, caption, icon, players, time, type, onFinish, waitingForGameMaster }) => {
  const timer = useTimer({
    initialTime: time,
    timerType: type
  })

  useEffect(() => {
    if (timer.time <= 0) {
      timer.pause()
      if (typeof (onFinish) === 'function') {
        onFinish()
      }
    }
  }, [timer.time])

  return (
    <Background align='flex-start'>
      <StyledContainer>
        <StyledHeader>
          <Typography variant='h6' gutterBottom>
            { title }
          </Typography>
        </StyledHeader>
        {
          icon
        }
        <StyledFunnyText>
          <Typography variant='h6' gutterBottom>
            { caption }
          </Typography>
        </StyledFunnyText>

        {
          players && <Footer>
            <PlayerStatus disableDarker={waitingForGameMaster} players={players} />
          </Footer>
        }
      </StyledContainer>
    </Background>
  )
}

Status.defaultProps = {
  time: 5,
  type: 'DECREMENTAL'
}

Status.propTypes = {
  title: PropTypes.string.isRequired,
  caption: PropTypes.array.isRequired,
  icon: PropTypes.node,
  players: PropTypes.array,

  time: PropTypes.number,
  type: PropTypes.oneOf([
    'DECREMENTAL',
    'INCREMENTAL'
  ]),
  onFinish: PropTypes.func,
  waitingForGameMaster: PropTypes.bool
}

export default Status
