import React, { useState, useRef, useEffect } from 'react'
import PropTypes from 'prop-types'

import { UseAuthContext } from './auth'
import { withRouter } from 'react-router-dom'

import socketIOClient from 'socket.io-client'

import * as actions from './actions'
import * as gameActions from './game-actions/actions'

import get from 'lodash/get'
import ReactNotification from 'react-notifications-component'

import 'react-notifications-component/dist/theme.css'

const ShootsContext = React.createContext({})

const useShootsContext = () => {
  const context = React.useContext(ShootsContext)
  if (!context) {
    throw new Error('No context found for useShootsContext')
  }

  return context
}

const socket = socketIOClient('http://localhost:5000')

const ShootsProviderContext = ({ children, history }) => {
  const auth = UseAuthContext()
  const notificationRef = useRef()

  const [gameState, setGameState] = useState({})
  const [shootsGame, setShootsGame] = useState({})

  useEffect(() => {
  // MAIN STATE
    socket.on('set-game-state', (res) => {
      console.log('setting game state...', res)
      setGameState((prev) => ({ ...prev, ...res }))
    })

    // game specific state
    socket.on('set-gameclass-state', (res) => {
      console.log('setting shoots game state...', res)
      setShootsGame((prev) => ({ ...prev, ...res }))
    })

    socket.on('notification', (res) => {
      pushNotification(res)
    })

    socket.on('created-game', (res) => {
      console.log('context created game', res)

      setGameState(prevState => {
        return { ...prevState, ...res }
      })
    })

    socket.on('game-destroyed', mofo => {
      console.log('1')
      pushNotification({
        title: 'Fuck!',
        message: `${mofo} destroyed the game!`,
        type: 'danger'
      })
      history.push('/')
    })

    socket.on('error-code', res => {
      if (res.status === 402) {
        auth.logout()
      }
    })

    socket.on('player-joined-game', (res) => {
      console.log('PLAYER JOINED', res)
      setGameState(prevState => {
        return { ...prevState, ...res }
      })
      history.push('/waiting')
    })

    socket.on('error', (res) => {
      console.log('ERORROROROOR', res)
      console.error(res.message)
    })
  }, [])

  const pushNotification = (res) => {
    notificationRef.current.addNotification({
      title: res.title,
      message: res.message,
      type: res.type,
      insert: res.insert || 'top',
      container: res.container || 'top-right',
      animationIn: ['animated', 'fadeIn'],
      animationOut: ['animated', 'fadeOut'],
      dismiss: { duration: res.duration || 2000 },
      dismissable: { click: true }
    })
  }

  const isGameMaster = () => {
    return get(shootsGame, 'currentGameMaster.id') === get(gameState, 'playerId')
  }

  const destroyGame = () => {
    setGameState({})
    setShootsGame({})
  }

  const getWinner = () => {
    let quickestTime = { time: Infinity, playerId: null }

    shootsGame.responses.forEach(response => {
      if (quickestTime.time > response.time && response.answer === shootsGame.getCurrentQuestion.correct) {
        quickestTime = response
      }
    })

    if (quickestTime.playerId) {
      return gameState.players.find(player => player.id === quickestTime.playerId)
    } else {
      const gameMaster = gameState.players.find(player => player.id === shootsGame.currentGameMaster.id)
      return gameMaster
    }
  }

  const bindedActions = []
  Object.entries(actions).forEach(entry => {
    const { type } = entry[1]()
    bindedActions[entry[0]] = data => socket.emit(type, { auth, data })
  })

  const bindedGameActions = []
  Object.entries(gameActions).forEach(entry => {
    const { type } = entry[1]()
    bindedGameActions[entry[0]] = data => socket.emit('update-game-status', { auth, data, type })
  })

  console.log('GAMESTATE -----', gameState)
  console.log(shootsGame)

  return (
    <ShootsContext.Provider
      value={{
        isHost: gameState.hostId === gameState.playerId,
        setGameState,
        gameState,
        shootsGame,
        actions: bindedActions,
        gameActions: bindedGameActions,
        isGameMaster: isGameMaster,
        getWinner: getWinner,
        destroyGame: destroyGame,
        pushNotification: pushNotification,
        isWinner: () => getWinner().id === gameState.playerId
      }}
      >
      <ReactNotification ref={notificationRef} />
      {children}
    </ShootsContext.Provider>
  )
}

ShootsProviderContext.propTypes = {
  children: PropTypes.any.isRequired,
  history: PropTypes.object.isRequired
}

export { useShootsContext, ShootsContext }
export default withRouter(ShootsProviderContext)
