import React, { memo } from 'react'
import PropTypes from 'prop-types'

import { withStyles } from '@material-ui/core'
import { GoogleButton, GuestButton, FaceBookButton, UseAuthContext } from '../../context/auth'
import HelloTitle from '../../context/auth/consumers/hello-title'
import Background from '../background'

const styles = theme => ({
  wrapper: {
    'box-sizing': 'border-box',
    'display': 'flex',
    'flex-direction': 'column',
    'justify-content': 'center',
    'align-items': 'center',
    'min-height': '100vh'
  },
  card: {
    maxWidth: 377,
    paddingBottom: 20
  },
  cardContent: {
    paddingBottom: 0
  },
  emoji: {
    fontSize: 40
  },
  intro: {
    textTransform: 'uppercase'
  }
})

const Login = ({ classes, history }) => {
  const { authenticated } = UseAuthContext()

  if (authenticated) {
    history.replace('/')
    return null
  }

  return <Background showGradient showCircles align='center'>
    <div className={classes.wrapper}>
      <HelloTitle />
      <FaceBookButton />
      <GoogleButton />
      <GuestButton />
    </div>
  </Background>
}

Login.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired
}

export default memo(withStyles(styles)(Login))
