
import React, { memo, useState } from 'react'
import QuizPart from './quiz-screen'

import { Parts } from './quiz-part'
import LeaderBoard from './leaderboard'

const Quiz = () => {
  const [currentPart, SetCurrentPart] = useState(0)

  if (currentPart >= Parts.length) {
    // need to come from server, just testing
    return <LeaderBoard scoreboard={
      [
        { name: 'johnny', score: 10 },
        { name: 'jildert', score: 0 }
      ]}
    />
  }
  return (
    <QuizPart
      // time left
      time={3000}
      quizPart={Parts[currentPart]}
      onFinish={() => SetCurrentPart(currentPart + 1)}
      currentPart={currentPart}
      totalParts={Parts.length}
    />
  )
}

export default memo(Quiz)
