import { keyframes } from 'styled-components'
const radius = '6px'
const smallRadius = '4px'

export const StarAnimation = keyframes`
   0% {
    transform: scale(1);
  }
  
   50% {
    transform: scale(1.15) rotate(-10deg);
    opacity: 1;
  }
  
    100% {
    transform: scale(1);
  }
`

export const CirclesAnimation = keyframes`
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.02);
    opacity: 0.8;
  }

  100% {
    transform: scale(1);
  }
`

export const TrophyAnimation = keyframes`
 0% {
    transform: scale(1);
    transform: scale(1.02) rotate(20deg);
  }

  50% {
    transform: scale(1.02) rotate(28deg);
  }

  100% {
    transform: scale(1);
    transform: scale(1.02) rotate(20deg);
  }
`

export const FollowPath = keyframes`
  0% { 
    transform: rotate(0deg) translate(${radius}) rotate(0deg); 
  }
  100% { 
    transform: rotate(360deg) translate(${radius}) rotate(-360deg); 
  }
`

export const FollowPathReversed = keyframes`
  0% { 
    transform: rotate(360deg) translate(${smallRadius}) rotate(-360deg); 
  }
  100% { 
    transform: rotate(0deg) translate(${smallRadius}) rotate(0deg); 
  }
`
