import React, { memo, useRef, useEffect } from 'react'
import ConfettiCannon from './index'

const Confetti = () => {
  const canvasRef = useRef()
  useEffect(() => {
    const fireConfetti = () => {
      const confetti = new ConfettiCannon(canvasRef.current)

      const settings = {
        amount: 219,
        angle: 284,
        dpr: 1,
        velocity: 8000,
        x: 10,
        y: 2000
      }

      confetti.fire(settings)
    }
    setTimeout(fireConfetti, 400)
    return () => clearTimeout(fireConfetti)
  }, [])

  return <canvas style={{ 'pointer-events': 'none', position: 'absolute', width: '100%', height: '100%', zIndex: 9999 }} ref={canvasRef} />
}

export default memo(Confetti)
