import React, { memo, useState, useEffect, useContext } from 'react'
import PropTypes from 'prop-types'
import randomName from './utils/random-name'

const AuthContext = React.createContext({})

let firstAuth = null

const AuthProvider = ({ children }) => {
  if (!firstAuth) {
    const authObject = window.localStorage.getItem('authObject')
    firstAuth = {}
    if (authObject) {
      firstAuth = JSON.parse(authObject)
    }
  }

  const [image, setImage] = useState(firstAuth.image)
  const [name, setName] = useState(firstAuth.name)
  const [parms, setParms] = useState(firstAuth)

  // get google account info from local storage on mount if existst
  useEffect(() => {
    const authObject = window.localStorage.getItem('authObject')
    if (authObject && !firstAuth.name) {
      const parsed = JSON.parse(authObject)
      setParms(parsed)
      setImage(parsed.image)
      setName(parsed.name)
    }
  }, [])

  const succesGoogle = parms => {
    if (!parms || !parms.profileObj) {
      return
    }

    window.localStorage.setItem('authObject', JSON.stringify({
      ...parms,
      name: parms.profileObj.name,
      image: parms.profileObj.imageUrl,
      google: true
    }))
    console.log('Signed in with google', parms)
    setParms({ ...parms, google: true })
    setImage(parms.profileObj.imageUrl)
    setName(parms.profileObj.name)
  }

  const succesFacebook = parms => {
    if (!parms.name) {
      return // user pressed cancel
    }

    window.localStorage.setItem('authObject', JSON.stringify({
      ...parms,
      name: parms.name,
      image: parms.picture.data.url,
      facebook: true
    }))

    setParms({ ...parms, facebook: true })
    console.log('Signed in with facebook', parms)
    setImage(parms.picture.data.url)
    setName(parms.name)
  }

  const setGuest = (name) => {
    const guestName = name || randomName()
    const escapedName = name.split(' ').join('-')
    const avatar = `https://api.adorable.io/avatars/60/${escapedName}.png`

    window.localStorage.setItem('authObject', JSON.stringify({
      ...parms,
      name: guestName,
      image: avatar,
      guest: true
    }))

    setName(guestName)
    setImage(avatar)
    setParms({ guest: true })
  }

  const logout = () => {
    setName(null)
    setImage(null)
    window.localStorage.removeItem('authObject')
    setParms({ })
  }

  window.logout = logout

  const formattedName = name && name.length > 15 ? name.split(' ')[0] : name

  return (
    <AuthContext.Provider value={{
        succesFacebook,
        succesGoogle,
        setGuest,
        parms,
        name: formattedName,
        image,
        logout,
        // login on anything
        authenticated: !!parms.google || !!parms.facebook || !!parms.guest,
        google: !!parms.google,
        facebook: !!parms.facebook,
        guest: !!parms.guest
    }}>
      { children }
    </AuthContext.Provider>
  )
}

// hookie
const UseAuthContext = () => useContext(AuthContext)

AuthProvider.propTypes = {
  children: PropTypes.node.isRequired
}

export { UseAuthContext }
export default memo(AuthProvider)
