import React, { useState, useEffect } from 'react'

import { withRouter } from 'react-router'
import { Link } from 'react-router-dom'
import PropTypes from 'prop-types'

import { UseAuthContext } from '../../context/auth/auth-context'
import { useShootsContext } from '../../context/shoots-context'
import { GoogleButton, GuestButton, FaceBookButton } from '../../context/auth'

import styled from 'styled-components'
import { withStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'

import Background from '../background'

import Modal from '../modal'
import Button from '../../core/button'

import Logo from '../logo'
import { Icons } from '../../helpers/icons'

import VolumeControl from '../volume-control'

import InstructionsDialog from '../instructions-stepper'
import tutorialSteps from '../instructions-stepper/instructions'
import Slide from '@material-ui/core/Slide'

const styles = theme => ({
  button: { margin: theme.spacing(1) },
  link: {
    color: theme.palette.primary.light,
    fontSize: 14,
    'line-height': '21px',
    fontWeight: 'bold',
    '&:hover': {
      fontWeight: 700,
      color: theme.palette.primary.light
    }
  }
})

const UltimateWrapper = styled.div`
  /* REFACTOR 'absolute' otherwise text will disappear */
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`

const ButtonsHolder = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px 0;
  overflow: hidden;
`

const HowTo = styled.div`
  position: absolute;
  top: -10px;
  right: 10px;
  cursor: pointer;
`

const Volume = styled.div`
  position: absolute;
  top: -30px;
  left: 0px;
  cursor: pointer;
`

const StyledSubtitle = styled.div`
  margin-top: -28px;
`

const Transition = React.forwardRef(function Transition (props, ref) {
  return <Slide direction='up' ref={ref} {...props} />
})

const Start = ({ classes, history }) => {
  const { name, image, google, facebook, guest, authenticated, logout } = UseAuthContext()
  const [ openLogoutDialog, setLogoutDialog ] = useState(false)

  const { gameState, destroyGame } = useShootsContext()
  const [open, setOpen] = React.useState(false)

  const logoutDialog = {
    title: 'Hi!',
    content: 'Do you want to join a game? 😊',
    buttons: [{
      title: 'Logout',
      color: 'red',
      onClick: () => {
        logout()
        setLogoutDialog(false)
      }
    }, {
      title: 'Join',
      color: 'orange',
      onClick: () => { history.push('/pin') }
    }
    ]
  }

  useEffect(() => {
    destroyGame()
  }, [])

  console.log(name, image, google, facebook, guest)
  console.table('CONTEXT --->', gameState)

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <Background showCircles showGradient titlescreen align='center'>
      <UltimateWrapper>
        {
          authenticated && <>
            <HowTo onClick={() => { setOpen(true) }}>{ Icons.questionmark }</HowTo>
            <Volume>
              <VolumeControl />
            </Volume>
          </>
        }
        <Logo />
        <StyledSubtitle>
          <Typography variant='h6' gutterBottom>THE GAME</Typography>
        </StyledSubtitle>

        <ButtonsHolder id='menu-list'>
          {
          authenticated ? <React.Fragment>

            <Button
              title={`Hi ${name}!`}
              icon={image}
              background='blue'
              onClick={() => { setLogoutDialog(true) }}
              iconPosition='right'
            />

            <Button
              title='new game'
              background='pink'
              onClick={() => { history.push('/categories') }}
            />

            <Button
              title='pin'
              background='orange'
              onClick={() => { history.push('/pin') }}
            />

            <Button
              title='scoreboard'
              background='green'
              onClick={() => { history.push('/leaderboard') }}
            />

          </React.Fragment> : <React.Fragment>
            <FaceBookButton />
            <GoogleButton />
            <GuestButton />
          </React.Fragment>
          }
        </ButtonsHolder>

        <Typography variant='body1' gutterBottom align='center'>
            Met het spelen van dit spel ga je akkoord met&nbsp;
          <Link className={classes.link} to='/home'>Terms of use</Link>&nbsp;
          <Link className={classes.link} to='/home'>Privacy Policy</Link>&nbsp;en&nbsp;
          <Link className={classes.link} to='/home'>Regels</Link>
        </Typography>

        <Modal
          onClose={() => { setLogoutDialog(false) }}
          isShowing={openLogoutDialog}
          content={logoutDialog}
        />

        <InstructionsDialog open={open} steps={tutorialSteps} transition={Transition} onClose={handleClose} fullScreen />

      </UltimateWrapper>
    </Background>
  )
}

Start.propTypes = {
  history: PropTypes.object,
  classes: PropTypes.object.isRequired
}

export default withRouter(withStyles(styles)(Start))
