import React, { memo, useEffect } from 'react'
import PropTypes from 'prop-types'

import styled from 'styled-components'
import { withStyles } from '@material-ui/core/styles'
import { useShootsContext } from '../../context/shoots-context'

import Typography from '@material-ui/core/Typography'
import Grid from '@material-ui/core/Grid'
import PlayersList from '../players-list'
import TextField from '@material-ui/core/TextField'
import { CircularProgress } from '@material-ui/core'
import Background from '../background'
import { withRouter } from 'react-router'
import { Icons } from '../../helpers/icons'

import Loader from '../../core/loader'

// import LogoutButton from '../context/auth/consumers/logout-button'

const styles = theme => ({
  root: {
    marginRight: theme.spacing(),
    marginLeft: theme.spacing()
  },
  input: {
    background: theme.palette.background.dark,
    color: theme.palette.primary.light,
    fontSize: 32,
    width: 160,
    textAlign: 'center',
    letterSpacing: theme.spacing()
  }
})

const Container = styled.div`
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
  width: 100%;

`

const CaptionBar = styled.div`
    width: calc(100% + 32px);
    margin: -16px;
    background-color: rgba(0.1, 0.1, 0.1, 0.2);

    margin-top: 32px;
`

const Wrapper = styled.div`
  width: 100%;
  overflow: hidden;
  background: ${({ theme }) => theme.palette.background.dark};
  padding: ${({ theme }) => theme.spacing(2) + 'px'};
`
const StyledTitle = styled(Typography)`
  text-transform: capitalize;
`

const BigPlayerImage = styled.div`
    width: 120px;
    height: 120px;
    background-image: url(${({ src }) => src});
    background-size: cover;
    margin: 0 auto;
    border-radius: 100%;
`

const Waiting = ({ classes, history, waiting, players, responses, title, winner, getCurrentQuestion, gameMaster, waitingForPlayers }) => {
  const { gameState, shootsGame, drinks } = useShootsContext()

  useEffect(() => {
    // game started
    if (waiting && shootsGame.currentGameMaster) {
      history.push('/countdown')
    }
  }, [shootsGame.currentGameMaster])

  // REFACTOR
  // should use React.Suspense with fallback laoder in the Routes comp
  if (players) {
    return (
      <Background showCircles >
        <Container>
          <Grid
            container
            direction='column'
            justify='center'
            alignItems='center'
          >
            {
              <Wrapper>
                {
                    waiting && <StyledTitle variant='h6'>{gameState.categoryName}</StyledTitle>
                }
                <Typography variant='h6' gutterBottom align='center'>
                  {
                    title || (waiting && 'Nodig je vrienden uit met deze pin')
                  }
                </Typography>

                {
                    waiting ? <TextField
                      inputProps={{ className: classes.input, maxLength: 5 }}
                      InputLabelProps={{ shrink: false }}
                      value={gameState.pin}
                      variant='outlined'
                      margin='normal'
                      label=''
                      id='pin-control'
                      disabled
                    /> : <>
                      {Icons.crown}
                      <BigPlayerImage src={winner && winner.image} />
                    </>
                }
              </Wrapper>
              }

            {
                !waiting && <CaptionBar>
                  <Typography variant='h6'>{winner && winner.name} hoeft niet te drinken!</Typography>
                </CaptionBar>
            }
            <PlayersList
              pin={gameState.pin}
              waiting={waiting}
              players={players}
              responses={responses}
              currentQuestion={getCurrentQuestion}
              gameMaster={gameMaster}
              drinks={drinks}
              />

            {waitingForPlayers && <Loader />}
          </Grid>
        </Container>
      </Background>
    )
  }

  return <CircularProgress />
}

Waiting.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  waiting: PropTypes.bool,
  players: PropTypes.array,
  responses: PropTypes.array,
  title: PropTypes.string,
  winner: PropTypes.object,
  getCurrentQuestion: PropTypes.object,
  gameMaster: PropTypes.object,
  waitingForPlayers: PropTypes.bool
}

export default memo(withRouter(withStyles(styles)(Waiting)))
