import React from 'react'
import styled from 'styled-components'
import Button from '../../core/button'

import LogoutButton from '../../context/auth/consumers/logout-button'
import { UseAuthContext } from '../../context/auth'

const Wrapper = styled.div`

`

const WelcomePage = ({ history }) => {
  const { authenticated } = UseAuthContext()

  if (!authenticated) {
    history.replace('/login')
    return null
  }

  return <div className='container'>
    <Wrapper>
      <LogoutButton />
      <Button title='Join game' background='orange' onClick={() => history.push('/')} />
      <Button title='Scoreboard' background='green' onClick={() => history.push('/scores')} />
      <Button title='New game' background='pink' onClick={() => history.push('/create')} />
    </Wrapper>
  </div>
}

export default WelcomePage
