const SlaveQuiz = ({ history }) => {
  // not game master
  const { gameActions, gameState, shootsGame } = useShootsContext()

  const playersExceptGameMaster = gameState.players.filter(player => player.id !== shootsGame.currentGameMaster.id)

  useEffect(() => {
    if (shootsGame.responses.length === playersExceptGameMaster.length) {
      history.push('/round-done')
    }
  })

  return <Background showCircles showGradient titlescreen aliv cgn='center'>
    <QuizScreen
      time={10000}
      question={shootsGame.getCurrentQuestion.title}
      answers={shootsGame.getCurrentQuestion.answers}
      correct={shootsGame.getCurrentQuestion.correct}
      onSelect={(answerIndex, time) => gameActions.slaveAnswer({
        'playerId': gameState.playerId,
        'time': time,
        'answer': answerIndex
      })}
      onFinish={() => history.push('/round-done')}
    />
  </Background>
}

SlaveQuiz.propTypes = {
  history: PropTypes.object.isRequired
}

export default SlaveQuiz
