import React, { memo } from 'react'

import Button from './../../../core/button'
import GoogleLogin from 'react-google-login'
import ProfilePicture from './profile-picture'
import { UseAuthContext } from '../auth-context'

const GoogleButton = () => {
  const { succesGoogle, image, google, name } = UseAuthContext()
  return (
    <GoogleLogin
      clientId='6874200645-p7vpj9t2s79s5n8mg9fvpqn52d99a275.apps.googleusercontent.com'
      onSuccess={succesGoogle}
      onFailure={console.log}
      render={renderProps => (
        <Button
          title={google && name ? `Hi ${name}!` : 'Google'}
          background='google'
          icon={google && image ? <ProfilePicture image={image} /> : 'Google'}
          iconPosition='right'
          onClick={renderProps.onClick}
        />
        )}
        />
  )
}

export default memo(GoogleButton)
