import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import Status from './status'

import { Funny } from './messages'
import { withRouter } from 'react-router'
// import SVGIcon from './svg-icon'
// import { Icons } from '../../helpers/icons'

import { useShootsContext } from '../../context/shoots-context'
import WaitIcon from './wait-timer'

// const getRandomFromArray = xs => xs[Math.floor(Math.random() * xs.length)]
const getRandomFunnySentences = () => Funny.slave

const CountDown = ({ history }) => {
  const { isGameMaster } = useShootsContext()
  const gameMaster = isGameMaster()

  const [index, setIndex] = useState(0)

  useEffect(() => {
    const interval = setInterval(() => {
      setIndex(index + 1)
    }, 2000)
    return () => clearInterval(interval)
  }, [])

  const startGame = () => {
    if (gameMaster) {
      history.push('/quiz-gamemaster')
    } else {
      history.push('/slave-waiting')
    }
  }

  return (
    <Status
      title='Momentje'
      caption={getRandomFunnySentences()[index]}
      icon={<WaitIcon onFinish={startGame} time={5} />}
    />
  )
}

CountDown.propTypes = {
  history: PropTypes.object.isRequired
}

export default withRouter(CountDown)
