const Scores = ({ classes, history }) => {
  const { getWinner, gameState, shootsGame, gameActions, isWinner } = useShootsContext()
  const player = gameState.players.find(p => p.id === gameState.playerId)

  const gameMaster = gameState.players.find(player => player.id === shootsGame.currentGameMaster.id)
  const winner = getWinner()

  // console.log('REMOVE THIS LINE GODEVERDEOMME')
  // const winner = gameMaster
  const gameMasterHasWon = gameMaster.id === winner.id

  const myDrinks = shootsGame.drinks && Object.keys(shootsGame.drinks).length > 0 ? (shootsGame.drinks[gameState.playerId] || 0) : ''
  const { toggle, isShowing } = useModal(false)
  const [modalShown, setModalIsShown] = useState(false)

  const doneClick = () => {
    toggle()
    gameActions.readyForNextRound(player.id)
  }

  useEffect(() => {
    // winner is altijd ready
    if (winner.id === gameState.playerId) {
      gameActions.readyForNextRound(player.id)
    }
  }, [])

  useEffect(() => {
    if (!isWinner() && shootsGame.drinks && Object.keys(shootsGame.drinks).length > 0 && !modalShown && !isShowing) {
      setModalIsShown(true)
      toggle()
    }
  })

  useEffect(() => {
    if (shootsGame.everyOneReady) {
      setTimeout(() => {
        if (shootsGame.gameFinished) {
          history.push('/game-end')
        } else {
          history.push('/countdown')
          if (player.isHost) {
            gameActions.nextQuestion()
          }
        }
      }, 500)
    }
  })

  const playersExceptWinner = gameState.players.filter(p => p.id !== winner.id)

  const modalContent = {
    title: `${player.name}!`,
    content: <>
      {
        myDrinks > 0
          ? <span>Je moet {myDrinks} shots nemen! (als je nog kan)</span>
          : <span>Gefeliciteerd!🎉</span>
      }
      <Grid container style={{ width: '50%', margin: '0 auto', marginTop: 24 }} alignItems='center' justify='center'>
        {
          myDrinks > 0 ? <>
            <Grid xs={6}>
              <Typography style={{ color: '#F79D1B', fontSize: 38, fontWeight: 'bold' }}>
                {myDrinks}x
              </Typography>
            </Grid>
            <Grid xs={6}>
              {Icons.drink}
            </Grid>
          </>
        : <Grid xs={6}>
          <Typography style={{ color: '#F79D1B', fontSize: 38, fontWeight: 'bold' }}>
            Je hoeft niet te drinken.
          </Typography>
        </Grid>
      }
      </Grid>
    </>,
    buttons: [
      {
        title: 'Done!',
        color: 'blue',
        onClick: doneClick
      }
    ]
  }

  return <>
    <Modal isShowing={isShowing} content={modalContent} />
    <WaitRoom
      title={gameMasterHasWon ? 'De Gamemaster wint!' : `${winner.name} was het snelst!`}
      players={playersExceptWinner}
      winner={winner}
      gameMaster={gameMaster}
      responses={shootsGame.responses}
      getCurrentQuestion={shootsGame.getCurrentQuestion}
      waitingForPlayers
    />
  </>
}

Scores.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired
}

export default memo(Scores)
