import styled from 'styled-components'

const CircleBackground = styled.circle`
  fill: #592cd1;
  stroke: ${props => props.strokeColor};
`
const CircleProgress = styled.circle`
  fill: none;
  stroke: ${props => props.strokeProgress ? props.strokeProgress : 100};
  stroke-linecap: round;
  stroke-linejoin: round;
  @keyframes dash {
    from {
      stroke-dashoffset: ${({ dashArray }) => dashArray || 100};
    }
    to {
      stroke-dashoffset: 0;
    }
}
`
const CircleText = styled.text`
  font-size: 36px;
  font-weight: bold;
  fill: white;
`

export {
  CircleBackground,
  CircleProgress,
  CircleText
}
