import React from 'react'
import PropTypes from 'prop-types'

import styled from 'styled-components'

const Circle = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: ${props => props.size}px;
  height: ${props => props.size}px;
  background-color: ${props => props.color};
  border: ${({ strokeColor, strokeWidth }) => `${strokeWidth}px solid ${strokeColor}`};
  border-radius: 50%;
  overflow: hidden;
`

const IconsWrapper = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
`

const MainIconWrapper = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`

const SmallStarWrapper = styled.div`
  position: absolute;
  top: ${({ top }) => top}px;
  left: ${({ left }) => left}px;
  width: 20px;
  height: 20px;
`

const SmallStar = styled.span`
  width: 15px;
  height: 15px;  
  display: block;
`

const ExtraSmallCircle = styled.svg`
  position: absolute;
  top: ${({ top }) => top}px;
  right: ${({ right }) => right}px;
  width: ${({ width }) => width}px;
  height: ${({ height }) => height}px;
  opacity: 0.4;
  color: ${props => props.color};
  border: 3px solid #ffffffca;
  border-radius: 50%;
`

const SVGIcon = ({ showIcons, icon, size, strokeColor, strokeWidth, color }) => {
  const center = (size / 2) - (10 / 2) - 4
  return (
    <Circle
      size={size}
      color={color}
      strokeColor={strokeColor}
      strokeWidth={strokeWidth}
      >
      <IconsWrapper>
        {
        showIcons && <React.Fragment>
          <SmallStarWrapper top={60} left={80}>
            <SmallStar>
              <svg id='star'
                viewBox='0 0 291.3 277.3'>
                <polygon fill='white' points='145.5,0.2 185.2,99.4 291,105.9 209.7,174.9 235.4,276.9 145.5,221.6 55.6,276.9 81.2,174.9 0,105.9 105.8,99.4 ' />
              </svg>
            </SmallStar>
          </SmallStarWrapper>

          <SmallStarWrapper top={-2} left={42}>
            <SmallStar>
              <svg id='star'
                viewBox='0 0 291.3 277.3'>
                <polygon fill='white' points='145.5,0.2 185.2,99.4 291,105.9 209.7,174.9 235.4,276.9 145.5,221.6 55.6,276.9 81.2,174.9 0,105.9 105.8,99.4 ' />
              </svg>
            </SmallStar>
          </SmallStarWrapper>

          <SmallStarWrapper top={50} left={10}>
            <SmallStar>
              <svg id='star'
                viewBox='0 0 291.3 277.3'>
                <polygon fill='white' points='145.5,0.2 185.2,99.4 291,105.9 209.7,174.9 235.4,276.9 145.5,221.6 55.6,276.9 81.2,174.9 0,105.9 105.8,99.4 ' />
              </svg>
            </SmallStar>
          </SmallStarWrapper>
          <ExtraSmallCircle width={10} height={10} top={40} right={6} />
          <ExtraSmallCircle width={10} height={10} top={(size / 2) + (size / 4) + 2} right={center} />
        </React.Fragment>
      }
        <MainIconWrapper>
          {
            icon
          }
        </MainIconWrapper>
      </IconsWrapper>
    </Circle>
  )
}

export default SVGIcon

SVGIcon.defaultProps = {
  size: 120,
  color: '#592cd1',
  strokeWidth: 6,
  strokeColor: '#FFFFFF',
  strokeProgress: '#7C56E9',
  showIcons: false
}

SVGIcon.propTypes = {
  size: PropTypes.number,
  icon: PropTypes.any,
  color: PropTypes.string,

  strokeWidth: PropTypes.number,
  strokeColor: PropTypes.string,
  showIcons: PropTypes.bool
}
