import React from 'react'
import PropTypes from 'prop-types'

import get from 'lodash/get'
import isEmpty from 'lodash/isEmpty'

import Player from './player'
import { useShootsContext } from '../../context/shoots-context'

const PlayerStatus = ({ players, disableDarker }) => {
  const { shootsGame } = useShootsContext()

  const playerIds = get(shootsGame, 'responses') ? shootsGame.responses.map(response => response.playerId) : []

  return (
    !isEmpty(players) && players.map(player => {
      // check if the playerid is also in the hasAnswered array, if so, player has answered
      const showCheckmark = playerIds.some(id => id === player.id)

      return <Player key={player.id} player={player} hasAnswered={showCheckmark} darker={!disableDarker} />
    })
  )
}

PlayerStatus.propTypes = {
  players: PropTypes.array,
  disableDarker: PropTypes.bool
}

export default PlayerStatus
