import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { withRouter } from 'react-router-dom'

import { withStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'

import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'

import { UseAuthContext } from '../../context/auth'
import { useShootsContext } from '../../context/shoots-context'

const styles = theme => ({
  root: {
    background: theme.palette.primary.light,
    marginRight: theme.spacing(1),
    marginLeft: theme.spacing(1)
  },
  input: { color: theme.palette.primary.dark },
  button: { margin: theme.spacing(1) }
})

const Secret = (props) => {
  const { name } = UseAuthContext()
  const { actions, socket } = useShootsContext()

  const [playerName, setPlayerName] = useState(name)
  const [gameId, setGameId] = useState('')
  const [infoMessage, setInfoMessage] = useState(null)

  const playerJoinGame = () => {
    actions.playerJoin({
      name: playerName,
      pin: gameId
    })
  }

  const playerLeavesGame = () => {
    actions.playerLeave()
  }

  socket.on('host-created-game', (data) => {
    setInfoMessage(data.message)
    setGameId(data.pin)
  })

  return <React.Fragment>
    <div className='container'>
      <div className='py-2 justify-content-center align-items-center'>
        <Typography variant='h6' gutterBottom>
          Welcome!
        </Typography>
        <Typography variant='subtitle2' gutterBottom>
          To get started. Check out the site on your computer to generate your unique sync code.
        </Typography>

        <Typography className='pt-4' variant='h6' gutterBottom>GOT A CODE? ENTER IT BELOW</Typography>

        {
         infoMessage !== null &&
         <div className='pt-2'>
           <Typography variant='subtitle1'>
            Info: {infoMessage}
           </Typography>
         </div>
        }

        <div className='py-2 justify-center align-items-center'>
          <TextField
            className={props.classes.root}
            inputProps={{ className: props.classes.input }}
            onChange={evt => setPlayerName(evt.target.value)}
            value={playerName}
            variant='filled'
            margin='normal'
            label='PlayerName'
            id='playerName'
          />
          <TextField
            className={props.classes.root}
            inputProps={{ className: props.classes.input }}
            value={gameId}
            onChange={evt => setGameId(evt.target.value)}
            variant='filled'
            margin='normal'
            label='Secret'
            id='gameId'
          />
        </div>

        <div className='pt-2 justify-center align-items-center'>
          <Button className={props.classes.button} variant='contained' color='secondary' onClick={playerJoinGame}>
            Join Game
          </Button>
          <Button className={props.classes.button} variant='contained' color='primary' onClick={playerLeavesGame}>
            Leave Game
          </Button>
        </div>

      </div>
    </div>
  </React.Fragment>
}

Secret.propTypes = {
  // history: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired
}

export default withRouter(withStyles(styles)(Secret))
