
import DatabaseTest from '../components/db-test'
import Start from '../components/start'
import Secret from '../components/secret'
import Login from '../components/login'
import Quiz from '../components/quiz'
import Welcome from '../components/welcome'
import Waiting from '../components/waiting'
import Leaderboard from '../components/leaderboard'
import Pin from '../components/pin'
import Categories from '../components/categories'
import Guest from '../components/guest'
import CountDown from '../components/status/countdown'

import GameMasterQuiz from '../components/shoots-quiz/game-master'
import SlaveQuiz from '../components/shoots-quiz/slave'
import Scores from '../components/scores'

import SlaveWaiting from '../components/status/slave-waiting'
import MasterWaiting from '../components/status/master-waiting'
import RoundDone from '../components/status/round-done'
import Winner from '../components/winner'

import { MD } from '../helpers/mobile-detect'

const defaultRoutes = [
  {
    path: '/quiz-gamemaster',
    component: GameMasterQuiz
  },
  {
    path: '/quiz-slave',
    component: SlaveQuiz
  },
  {
    path: '/scores',
    component: Scores
  },
  {
    path: '/slave-waiting',
    component: SlaveWaiting
  },
  {
    path: '/master-waiting',
    component: MasterWaiting
  },
  {
    path: '/round-done',
    component: RoundDone
  },
  {
    path: '/winner',
    component: Winner
  }
]

const desktopRoutes = [
  {
    path: '/',
    component: Start
  },
  {
    path: '/secret',
    component: Secret,
    footer: true
  },
  {
    path: '/database',
    component: DatabaseTest
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/welcome',
    component: Welcome
  },
  {
    path: '/pin',
    component: Pin,
    footer: true
  },
  {
    path: '/guest',
    component: Guest
  },
  {
    path: '/categories',
    component: Categories,
    footer: true
  },
  {
    path: '/leaderboard',
    component: Leaderboard,
    footer: true
  },
  {
    path: '/waiting',
    component: Waiting,
    showModal: true,
    footer: true
  },
  {
    path: '/quiz',
    component: Quiz,
    footer: true
  },
  {
    path: '/countdown',
    component: CountDown,
    footer: true
  }
]

const mobileRoutes = [
  {
    path: '/countdown',
    component: CountDown
  },
  {
    path: '/',
    component: Start
  },
  {
    path: '/secret',
    component: Secret,
    footer: true
  },
  {
    path: '/database',
    component: DatabaseTest
  },
  {
    path: '/login',
    component: Login
  },
  {
    path: '/welcome',
    component: Welcome
  },
  {
    path: '/pin',
    component: Pin,
    footer: true,
    showModal: false,
    buttons: {
      leftButton: {
        title: 'Annuleren'
      }
    }
  },
  {
    path: '/guest',
    component: Guest
  },
  {
    path: '/categories',
    component: Categories,
    footer: true,
    buttons: {
      leftButton: {
        title: 'Terug'
      }
    }
  },
  {
    path: '/leaderboard',
    component: Leaderboard,
    footer: true
  },
  {
    path: '/waiting',
    component: Waiting,
    footer: true,
    showModal: true
  },
  {
    path: '/quiz',
    component: Quiz,
    footer: true
  }
]

const getRoutes = () => {
  if (MD()) {
    return mobileRoutes
  }

  return desktopRoutes
}

export default () => ([ ...defaultRoutes, ...getRoutes() ])
