
import React, { memo, useState, useCallback, useEffect } from 'react'
import PropTypes from 'prop-types'

import styled from 'styled-components'

import TextField from '@material-ui/core/TextField'

import { withStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'

import Background from '../background'

import { UseAuthContext } from '../../context/auth'
import { useShootsContext } from '../../context/shoots-context'

import { withRouter } from 'react-router'

const Header = styled.div`
  overflow: hidden;
  margin-bottom:${props => props.theme.spacing(4) + 'px'};
  margin-top: -150px;
`

const styles = theme => ({
  root: {
    marginRight: theme.spacing(),
    marginLeft: theme.spacing()
  },
  input: {
    background: theme.palette.background.dark,
    color: theme.palette.primary.light,
    fontSize: 40,
    width: 210,
    textAlign: 'center',
    letterSpacing: theme.spacing()
  },
  button: { margin: theme.spacing() }
})

const Pin = ({ classes, history }) => {
  const { name } = UseAuthContext()
  const { actions } = useShootsContext()
  const [pin, setPin] = useState('')

  useEffect(() => {
    if (pin.length === 5) {
      playerJoinGame()
    }
  }, [pin])

  const playerJoinGame = useCallback(() => {
    actions.playerJoin({
      name: name,
      pin: parseInt(pin)
    })
  })

  return (
    <Background align='center'>
      <Header>
        <Typography variant='h6' gutterBottom align='center'>
        Voer de pin in die je hebt gekregen van de game master.
        </Typography>
      </Header>
      <TextField
        inputProps={{ className: classes.input, maxLength: 5 }}
        InputLabelProps={{ shrink: false }}
        onChange={evt => setPin(evt.target.value)}
        value={pin}
        variant='outlined'
        margin='normal'
        label=''
        id='pin-control'
      />
    </Background>
  )
}

Pin.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired
}

export default withRouter(memo(withStyles(styles)(Pin)))
