import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import Status from './status'

import SVGIcon from './svg-icon'
import { Icons } from '../../helpers/icons'

import { Funny } from './messages'
import { withRouter } from 'react-router'

import { useShootsContext } from '../../context/shoots-context'

// const getRandomFromArray = xs => xs[Math.floor(Math.random() * xs.length)]
const getRandomFunnySentences = () => Funny.slave

const MasterWaiting = ({ history }) => {
  const { gameState, shootsGame } = useShootsContext()

  const [index, setIndex] = useState(0)
  const [message, setMessage] = useState()

  useEffect(() => {
    getMessages()
  }, [])

  useEffect(() => {
    const interval = setInterval(() => {
      setIndex(index + 1)
    }, 2000)
    return () => clearInterval(interval)
  }, [])

  const getMessages = () => {
    const waitingCaption = `Iedereen moet nu antwoorden. Dit kan eventjes duren..`
    const captions = [waitingCaption, ...getRandomFunnySentences()]

    return setMessage(captions[index])
  }

  const playersExceptGameMaster = gameState.players.filter(player => player.id !== shootsGame.currentGameMaster.id)

  useEffect(() => {
    if (shootsGame.responses.length === playersExceptGameMaster.length) {
      history.push('/round-done')
    }
  })

  return (
    <Status
      title='Momentje'
      caption={message}
      icon={<SVGIcon icon={Icons.hourglass} />}
      players={playersExceptGameMaster}
    />
  )
}

MasterWaiting.propTypes = {
  history: PropTypes
}

export default withRouter(MasterWaiting)
