import React, { memo, useEffect } from 'react'
import PropTypes from 'prop-types'

import { withRouter } from 'react-router'
import { makeStyles } from '@material-ui/core/styles'

import Modal from '../modal'
import useModal from '../../hooks/useModal'

import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'

import { useShootsContext } from '../../context/shoots-context'

const useStyles = makeStyles(theme => ({
  appBar: {
    top: 'auto',
    bottom: 0,
    backgroundColor: theme.palette.background.light
  },
  grow: {
    flexGrow: 1
  },
  startButton: {
    color: theme.palette.primary.main
  },
  cancelButton: {
    color: theme.palette.secondary.main
  }
}))

const Footer = ({ history, children, showModal, buttons: { leftButton, rightButton } }) => {
  const classes = useStyles()
  const { toggle, isShowing } = useModal()

  const { isHost, actions, setGameState } = useShootsContext()

  const startGame = () => {
    actions.startGame()
  }

  // REFACTOR
  // make something more generic for 'ja' button
  const modalContent = {
    title: 'Spel verlaten',
    content: 'Weet je zeker dat je het spel wilt verlaten?',
    buttons: [
      {
        title: 'Nee',
        color: 'red',
        onClick: toggle
      },
      {
        title: 'Ja',
        color: 'blue',
        onClick: () => {
          leaveGame()
          toggle()
          // TODO
          // if gamemaster --> delete game and all players and send message to players
          // if player --> delete player from game and let the rest know player has left
        }
      }
    ]
  }

  const leaveGame = () => {
    actions.playerLeave()
    setGameState({})
    history.push('/')
  }

  useEffect(() => {
    return () => {
      // if (showModal) {
      //   leaveGame()
      // }
    }
  }, [])

  const handleCancelButton = () => {
    if (showModal) {
      toggle()
    } else {
      history.goBack()
    }
  }

  return (
    <React.Fragment>
      <AppBar position='fixed' color='secondary' className={classes.appBar}>
        <Toolbar>
          {
          children ||
          (
            <React.Fragment>
              {
                leftButton && (
                <Button className={classes.cancelButton} onClick={handleCancelButton}>
                  {leftButton.title}
                </Button>
              )}
              <div className={classes.grow} />
              {
                rightButton && isHost && (
                <Button onClick={startGame} className={classes.startButton} color='inherit'>
                  {rightButton.title}
                </Button>
            )}
            </React.Fragment>
          )
        }
        </Toolbar>
      </AppBar>
      <Modal onClose={toggle} isShowing={isShowing} content={modalContent} />
    </React.Fragment>
  )
}

Footer.defaultProps = {
  showModal: false,
  buttons: {
    leftButton: {
      title: 'Annuleren'
    },
    rightButton: {
      title: 'Start'
    }
  }
}

Footer.propTypes = {
  history: PropTypes.object.isRequired,
  children: PropTypes.any,
  buttons: PropTypes.object,
  showModal: PropTypes.bool
}

export default withRouter(memo(Footer))
